      title.setAttribute("id", `app-switcher-${AppSwitcherDirective.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `app-switcher-${AppSwitcherDirective.titleIdCounter}`);
